import {
  Component,
  OnInit,
  Input,
  HostListener,
  EventEmitter,
  ElementRef,
  Output,
} from "@angular/core";
import { IPost } from "../posts-interfaces";
import {
  trigger,
  state,
  style,
  animate,
  transition,
} from "@angular/animations";
import { debounce } from "../../core/utils";
import { LightboxConfig, Lightbox } from "ngx-lightbox";

@Component({
  selector: "app-posts-carousel",
  templateUrl: "./posts-carousel.component.html",
  styleUrls: [
    "./posts-carousel.component.scss",
    "./posts-carousel.component.responsivity.scss",
  ],
  animations: [
    trigger("fadeInOut", [
      state(
        "void",
        style({
          opacity: 0,
        })
      ),
      transition("void <=> *", animate(300)),
    ]),
  ],
})
export class PostsCarouselComponent implements OnInit {
  public _posts: IPost[] = [];
  public _posts2: IPost[] = [];
  public _originalPosts: IPost[] = [];
  public _currentPage: number;

  @Output() onResultsPerPageChanged = new EventEmitter<number>();

  resultsPerPage: number;
  elWidth: number;
  start: number;
  end: number;

  constructor(
    private elRef: ElementRef,
    private _lightboxConfig: LightboxConfig,
    private _lightbox: Lightbox
  ) {}

  @Input() get currentPage(): number {
    return this._currentPage;
  }

  set currentPage(value: number) {
    if (value) {
      this._currentPage = value;
      this.populateCarousel();
    }
  }

  @Input() get posts(): IPost[] {
    return this._posts;
  }

  set posts(value: IPost[]) {
    if (value) {
      this._originalPosts = value;
      this.onResizeElement();
    }
  }

  ngOnInit(): void {}

  @HostListener("window:resize")
  @debounce(25)
  onResize() {
    this.onResizeElement();
  }

  private onResizeElement(): void {
    this.elWidth = this.elRef.nativeElement.clientWidth;
    this.resultsPerPage = Math.ceil(this.elWidth / 465);

    this.populateCarousel();
  }

  private populateCarousel(): void {
    if (this._currentPage && this._posts) {
      this._posts = this._originalPosts.slice(0, 3);

      this._posts2 = this._originalPosts.slice(3);

      this.onResultsPerPageChanged.emit(this.resultsPerPage);
    }
  }

  open(source: string): void {
    // open lightbox
    if (source == "../../assets/posts/optx.png") {
      source = "../../assets/posts/optx-blur.png";
    }
    if (source == "../../assets/posts/mfo.png") {
      source = "../../assets/posts/mfo-blur.png";
    }
    const albums = [
      {
        src: source,
        caption: "",
        thumb: source,
      },
    ];
    this._lightbox.open(albums, 0);
  }

  close(): void {
    // close lightbox programmatically
    this._lightbox.close();
  }
}
